/**
 * Content List - Title Component.
 */

import React from 'react';
import PropTypes from 'prop-types';

/**
 * WP dependencies
 */
const {
    components: {
        TextControl,
    },
    i18n: {
        __,
        sprintf,
    },
    blockEditor: {
        RichText
    },
    element: {
        useState,
    }
} = wp;

/**
 * ContList - React Component
 */
function Footer(
    {
        footer,
        attrsChange,
    }
) {

    const [ urlDisplay, setUrlDisplay ] = useState( '' !== footer.text );

    const styles = { display: urlDisplay ? 'block' : 'none' };

    return (
        <>
            <div className='cl-block__title cl-block__grid'>
                <RichText
                    tagName="span"
                    multiline={ false }
                    placeholder={ __( 'Add call to action?', 'tenup' ) }
                    value={ footer.text }
                    className="tabitem-label"
                    allowedFormats={[]}
                    keepPlaceholderOnFocus={ true }
                    onChange={ text => {
                        setUrlDisplay( 0 < text.length );
                        attrsChange( { text }, 'footer', 'object' )
                    } }
                />
            </div>
            <div className='cl-block__title' style={ styles }>
                <TextControl
                    value={ footer.url }
                    placeholder={ sprintf( __( 'Add URL for "%s" text', 'tenup' ), footer.text ) }
                    type='url'
                    onChange={ url => attrsChange( { url }, 'footer', 'object' ) }
                />
            </div>
        </>
    );
}

Footer.propTypes = {
    footer: PropTypes.shape( {
        url: PropTypes.string,
        text: PropTypes.string,
        ctaText: PropTypes.string,
    } ),
    attrsChange: PropTypes.func,
};

export default Footer;
