/**
 * External dependencies
 */

const {
	compose: {
		withInstanceId
	},
	components: {
		Button,
	},
	i18n: {
		__,
		sprintf
	}
} = wp;

const {noop} = lodash;

/**
 * Content Select Component.
 *
 * @param status
 * @param title
 * @param index
 * @param isBorderless
 * @param disabled
 * @param onClickRemove
 * @param onMouseEnter
 * @param onMouseLeave
 * @param instanceId
 * @returns {*}
 * @constructor
 */
function TaxonomyTermsToken( {
	title,
	index,
	isBorderless = false,
	disabled,
	onClickRemove = noop,
	onMouseEnter,
	onMouseLeave,
	instanceId,
} ) {

	const tokenClasses = 'components-form-token-field__token cl-block__token';

	return (
		<div
			className={tokenClasses}
			{ ...{
				title,
				onMouseEnter,
				onMouseLeave,
			} }
		>
			<div
				className="components-form-token-field__token-text"
				id={`components-form-token-field__token-text-${instanceId}`}
			>
				<span aria-hidden="true">
					{title}
				</span>
			</div>

			<Button
				className="components-form-token-field__remove-token"
				icon="dismiss"
				onClick={() => onClickRemove( { index } )}
				label={sprintf( __( 'Remove %s', 'tenup' ), title )}
				aria-describedby={`components-form-token-field__token-text-${instanceId}`}
			/>
		</div>
	);
}

export default withInstanceId( TaxonomyTermsToken );
