/**
 * Content List - Title Component.
 */

import React from 'react';
import PropTypes from 'prop-types';

/**
 * WP dependencies
 */
const {
    components: {
        TextControl,
    },
    i18n: {
        __,
        sprintf,
    },
    blockEditor: {
        RichText
    },
    element: {
        useState,
    }
} = wp;

/**
 * ContList - React Component
 */
function Header(
    {
        title,
        attrsChange,
    }
) {

    const [ urlDisplay, setUrlDisplay ] = useState( '' !== title.text );

    const styles = { display: urlDisplay ? 'block' : 'none' };

    return (
        <>
            <div className='cl-block__title cl-block__grid'>
                <RichText
                    tagName="strong"
                    multiline={ false }
                    placeholder={ __( 'Add title?', 'tenup' ) }
                    value={ title.text }
                    className="tabitem-label"
                    allowedFormats={[]}
                    keepPlaceholderOnFocus={ true }
                    onChange={ text => attrsChange( { text }, 'title', 'object' ) }
                />
                <RichText
                    tagName="span"
                    multiline={ false }
                    placeholder={ __( 'Add call to action?', 'tenup' ) }
                    value={ title.ctaText }
                    className="tabitem-label"
                    allowedFormats={[]}
                    keepPlaceholderOnFocus={ true }
                    onChange={ ctaText => {
                        setUrlDisplay( 0 < ctaText.length );
                        attrsChange( { ctaText }, 'title', 'object' )
                    } }
                />
            </div>
            <div className='cl-block__title' style={ styles }>
                <TextControl
                    value={ title.url }
                    placeholder={ sprintf( __( 'Add URL for "%s" text', 'tenup' ), title.ctaText ) }
                    type='url'
                    onChange={ url => attrsChange( { url }, 'title', 'object' ) }
                />
            </div>
        </>
    );
}

Header.propTypes = {
    title: PropTypes.shape( {
        url: PropTypes.string,
        text: PropTypes.string,
        ctaText: PropTypes.string,
    } ),
    attrsChange: PropTypes.func,
};

export default Header;
